import * as React from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import NiceButton from "../NiceButton/NiceButton";
import bitcoinGif from "../../assets/icons/bitcoin.gif";
import { useSelector } from "react-redux";
import { useHistory } from "react-router";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

export const ModalContacts = () => {
  const loggedUser = useSelector((state) => state.userReducer.user);
  const history = useHistory();
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const IconBitcoin = () => <img src={bitcoinGif} alt="" />;

  const onGoContact = (contactId) => {
    handleClose();
    history.push(`/contact/${contactId}`);
  };

  return (
    <>
      <NiceButton
        onClick={handleOpen}
        Icon={IconBitcoin}
        className="bitcoin-gif"
      />
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Typography variant="h6" component="h2">
            Select Contact
          </Typography>

          {loggedUser.contacts.map((contact) => (
            <Typography onClick={() => onGoContact(contact._id)}>
              {contact.contactName}
            </Typography>
          ))}
        </Box>
      </Modal>
    </>
  );
};
